import React, { memo } from 'react';
import styled from 'styled-components';

import Paper from '@material-ui/core/Paper';
import { Box } from '@material-ui/core';
import { Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import { SingleLineTextField, StyledButton } from '../../atoms/index';
import { ChooseRole } from './ChooseRole';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      '& > *': {
        marginTop: theme.spacing(8),
        width: theme.spacing(50),
        height: theme.spacing(85),
      },
    },
  }),
);

const FlexBox = styled.div`
  display: flex;
`;

const StyledPaper = styled(Paper)`
  border-radius: 10px;
`;

export type Props = {
  role: string | null;
  email: string;
  password: string;
  firstName: string;
  lastName: string;
  gender: number | null;
  daycareId: number | null;
  telephoneNumber: string;
  onClickSignUp: () => void;
  onChangeRole: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onChangeEmail: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onChangeGender: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onChangePassword: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onChangeDaycareId: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onChangeLastName: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onChangeFirstName: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onChangeTelephoneNumber: (e: React.ChangeEvent<HTMLInputElement>) => void;
};
export const SignUpPaper: React.FC<Props> = memo((props) => {
  const {
    role,
    email,
    gender,
    lastName,
    firstName,
    password,
    daycareId,
    telephoneNumber,
    onClickSignUp,
    onChangeRole,
    onChangeEmail,
    onChangeGender,
    onChangePassword,
    onChangeLastName,
    onChangeFirstName,
    onChangeDaycareId,
    onChangeTelephoneNumber,
  } = props;

  const classes = useStyles();

  return (
    <div className={classes.root}>
      <StyledPaper elevation={3}>
        <Box component="h1" pt={5} textAlign="center">
          Sign UP
        </Box>
        <FlexBox>
          <Box textAlign="center" ml={4} mr={1}>
            <SingleLineTextField
              id="姓"
              textName="姓"
              isFullWidth={false}
              placeholder="親御様の姓"
              value={lastName}
              onChange={onChangeLastName}
            />
          </Box>
          <Box textAlign="center" mr={4} ml={1}>
            <SingleLineTextField
              id="名"
              textName="名"
              isFullWidth={false}
              placeholder="親御様の名"
              value={firstName}
              onChange={onChangeFirstName}
            />
          </Box>
        </FlexBox>
        <Box textAlign="center" m={4}>
          <SingleLineTextField
            id="電話番号"
            textName="電話番号"
            isFullWidth={true}
            placeholder="ハイフンなし"
            value={telephoneNumber}
            onChange={onChangeTelephoneNumber}
          />
        </Box>
        <Box textAlign="center" m={4}>
          <SingleLineTextField
            id="email"
            textName="メールアドレス"
            isFullWidth={true}
            placeholder="xxxxxxx@xxx.ne.jp"
            value={email}
            onChange={onChangeEmail}
          />
        </Box>
        <Box textAlign="center" m={4}>
          <SingleLineTextField
            id="password"
            textName="パスワード"
            isFullWidth={true}
            placeholder="6文字以上"
            value={password}
            onChange={onChangePassword}
            type="password"
          />
        </Box>
        <Box m={4}>
          <ChooseRole
            role={role}
            gender={gender}
            daycareId={daycareId}
            onChangeRole={onChangeRole}
            onChangeGender={onChangeGender}
            onChangeDaycareId={onChangeDaycareId}
          />
        </Box>
        <Box textAlign="center" mx={2}>
          <StyledButton
            onClick={onClickSignUp}
            variant="contained"
            label="登録"
            fontSize={20}
            width={90}
            borderRadius={20}
          />
        </Box>
      </StyledPaper>
    </div>
  );
});

SignUpPaper.displayName = 'SignUpPaper';
